<template>
  <div class="interface" ref="night">
    <div class="font-heard">
      <div class="bad"></div>
    </div>
    <div class="read-books" :id="this.booktext.cid">
      <div class="read-section">{{this.booktext.cname}}</div>
      <div class="read-text" @click="readfik()" @scroll.passive="getScroll($event)" ref="showHeight">
        <!-- 文本内容 -->
        <div
          ref="charHeight"
          :style="{top:this.sgitH+'px'}"
          class="character"
        >{{this.booktext.content}}</div>
      </div>
      <div class="read-pagination">
        <span>{{this.timer}}</span>
        <span>进度{{this.fvalue}}%</span>
      </div>
      <!-- 隐藏组件 -->
      <transition name="slide-down">
        <div v-show="vshow" class="read-return">
          <van-nav-bar left-text left-arrow @click-left="back" ref="navigation1" :fixed="true">
            <van-icon name="service-o" slot="right" color="#888" size="20" />
            <van-icon name="comment-o" dot slot="right" color="#888" size="20" />
            <van-icon name="ellipsis" slot="right" color="#888" size="20" />
          </van-nav-bar>
        </div>
      </transition>
      <transition name="slide-up">
        <div v-show="vshow" class="read-function" ref="navigation2">
          <div class="function-top">
            <div class="fun-text">
              <span>上一章</span>
              <span>下一章</span>
            </div>
            <!-- 进度条 -->
            <div class="fun-schedule">
              <van-slider @change="changclick(fvalue)" v-model="fvalue" active-color="#1FB18C" />
            </div>
          </div>
          <div class="function-bottom">
            <ul>
              <li>
                <div class="lis-logo" @click="cklabel(isAls)">
                  <van-icon name="comment-o" size="22" />
                  <P>目录</P>
                </div>
              </li>

              <li>
                <div class="lis-logo" @click="night(lighten)">
                  <van-icon name="eye-o" size="22" :color="lighten" :v-model="facs" />
                  <P>{{this.daytime}}</P>
                </div>
              </li>

              <li>
                <div class="lis-logo">
                  <van-icon name="setting-o" size="22" />
                  <P>设置</P>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <!-- 书签 -->
    
    <div class="read-label"  :class="{label :isAls}" ref="label" @click="fontnn($event)">
      <div class="books-label" ref="labelbook">
        <div class="label-top">
          <div class="label-fifle" ref="labelfifle">
            <div class="fifle-heard">
              <div class="fifle-books1">
                <div class="books1-fl">
                  <img :src="this.book.Img" class="max-img" />
                </div>
                <div class="books1-fr">
                  <h3>{{this.book.Name}}</h3>
                  <p>{{this.book.Author}}</p>
                  <p>{{this.book.Desc}}</p>
                </div>
              </div>
              <div class="fifle-books2">
                <span>目录</span>
                <div class="book2-logo">
                  <van-icon name="label-o" size="17" />
                </div>
              </div>
            </div>
          </div>
          <div class="label-uls" @scroll.passive="labelScoll($event)">
            <div class="fifle-uls">
              <ul>
                <li
                  v-for="(item,index) in catalogue"
                  :key="index"
                  :id="item.id"
                  @click="bannk(item.id,index)"
                >
                  <div class="fit-fl">
                    <span>{{item.name}}</span>
                  </div>
                  <div class="fit-fr">
                    <van-icon name="down" size="14" color="#D5D5D5" />
                  </div>
                </li>
              </ul>
            </div>
            <div class="assgin">
              
            </div>
          </div>
        </div>
        <div class="label-bottom">
          <div class="label-btn">
            <div class="btn1 cool">目录</div>
            <div class="btn2">书签</div>
          </div>
        </div>
      </div>
    </div>
  
  </div>
</template>

<script>
import { getTime } from "../../store/getTime.js";
export default {
  data() {
    return {
      //控制功能区
      vshow: false,
      height: 0,
      isAls: false,
      isBls: true,
      left: "-555px",
      book: "",
      booktext: "",
      sun: "",
      pos: "0",
      fvalue: 0,
      id: "",
      catalogue: "",
      // 内容id
      textId: "",
      //夜间模式
      daytime: "夜间",
      lighten: "",
      facs: true,
      sgitH: "",
      //默认章节
      index: 0,
      //时间
      timer: "",
    };
  },
  mounted() {
    //从缓存获取章节进度
    let bookindex = JSON.parse(localStorage.getItem("book-index"));
    this.booktext = this.sun[bookindex].data;
    this.sun = JSON.parse(localStorage.getItem("book-texts"));
    this.index=bookindex
  },
  methods: {
    //获取进度条
    changclick(fvalue) {
      let charHeight = this.$refs.charHeight.clientHeight;
      //获取文本屏幕显示高度
      let showHeight = this.$refs.showHeight.clientHeight;
      let showH = charHeight - showHeight;
      this.sgitH = -this.fvalue * (showH / 100);
      this.fvalue = fvalue;
    },
    readfik() {
      this.vshow = !this.vshow;
    },
    bannk(id, key) {
      this.Hlab = "none";
      this.textId = id;
      this.$refs.label.style.left = '-555px'
      //缓存章节进度
      localStorage.setItem("book-index", JSON.stringify(key));
      this.sun.forEach(v => {
        if (id == v.data.cid) {
          this.booktext = v.data;
        }
        return;
      });
    },
    cklabel(isAls) {
      this.isAls= true ;
      this.$refs.label.style.left = '0px'
      this.vshow = false;
      this.catalogue = JSON.parse(localStorage.getItem("catalogue"));
      //获取内容
      if (!this.id) {
        return;
      }
      //获取小说目录
      this.$axios({
        methods: "GET",
        dataType: "jsonp",
        url: "http://localhost:1000/getjson/xiaoshuo-" + this.id
      }).then(rel => {
        this.catalogue = rel.data.data.list[0].list;
        localStorage.setItem(
          "catalogue",
          JSON.stringify(rel.data.data.list[0].list)
        );
      });
    },
    fontnn(e) {

      if (e.target.className == "read-label label") {
         this.$refs.label.style.left = '-555px' ;
      }
    },
    getScroll(event) {
      //滚动距离
      let pos = parseInt(event.target.scrollTop);
      //内容高度
      let charHeight = this.$refs.charHeight.clientHeight;
      //显示内容高度
      let showHeight = this.$refs.showHeight.clientHeight;
      let scrollTG = (charHeight - showHeight) / 100;
      this.fvalue = parseInt(pos / scrollTG);
    },
    back() {
      this.$router.go(-1);
    },
    // 改变背景颜色
    night(lighten) {
      this.lighten = lighten == "" ? "#1FB18C" : "";
      this.facs = !this.facs;
      if (!this.facs) {
        this.$refs.night.style.backgroundColor = "#0D0D0D";
        this.$refs.night.style.color = "#828282";
        this.$refs.navigation1.style.backgroundColor = "#1A1A1A";
        this.$refs.navigation2.style.backgroundColor = "#1A1A1A";
        this.$refs.labelbook.style.background = "black";
        this.$refs.labelfifle.style.background = "black";
        this.$refs.labelbook.style.color = "white";
        this.daytime = "白天";
      } else {
        this.$refs.night.style.backgroundColor = "";
        this.$refs.night.style.color = "";
        this.$refs.navigation1.style.backgroundColor = "";
        this.$refs.navigation2.style.backgroundColor = "";
        this.$refs.labelbook.style.background = "white";
        this.$refs.labelfifle.style.background = "#f8f7fc";
        this.$refs.labelbook.style.color = "#000000";
        this.daytime = "夜间";
      }
    },
    //回顶部
    labelScoll(e){
      
    }
  },
  components: {},
  created() {
    this.sun = JSON.parse(localStorage.getItem("book-texts"));
    this.book = JSON.parse(localStorage.getItem("book-info"));
    let id = this.$route.params.id;
    this.id = id;
    //时间
    let time = new Date();
    this.timer = getTime(time);
    //获取内容
    if (!id) {
      return;
    }
    this.$axios({
      methods: "GET",
      dataType: "jsonp",
      url: "http://localhost:1000/getjson/zhang-" + this.id
    }).then(reche => {
      localStorage.setItem("book-texts", JSON.stringify(reche.data));
      this.sun = reche.data;
      this.booktext = reche.data[this.index].data;
    });
  }
};
</script>
<style lang="less">
p {
  margin: 0;
}
.sliow {
  display: none;
}
.upsliow {
  display: none;
}

h3 {
  margin: 0;
}
.max-img {
  display: block;
  width: 100%;
  height: 100%;
}
.interface {
  width: 100%;
  overflow: hidden;
  position: relative;
  background: #f7f7f7;
  .font-heard {
    width: 100%;
    overflow: hidden;
    position: sticky;
    left: 0;
    top: 0;
    z-index: 10;
  }
  .bad {
    width: 100%;
    height: 24px;
  }
  .read-books {
    width: 100%;
    margin: 0 auto;

    height: calc(667px - 24px);
    .read-text {
      width: calc(100% - 30px);
      height: calc(100% - 30px - 30px);
      overflow-y: auto;
      margin: 0 15px;
      position: relative;
      .character {
        width: 100%;
        position: absolute;
        left: 0;
        top: 0;
        overflow: hidden;
        font-size: 16px;
        text-indent: 30px;
      }
    }
    .read-section {
      width: calc(100% - 30px);
      margin: 0 auto;
      line-height: 30px;
      font-size: 12px;
    }
    .read-pagination {
      overflow: hidden;
      width: calc(100% - 30px);
      margin: 0 auto;
      line-height: 30px;
      span:nth-child(2) {
        float: right;
      }
    }

    .read-return {
      position: absolute;
      left: 0;
      top: 0;
      height: 50px;
      width: 100%;
      display: block;
      overflow: hidden;
      //  过渡动画
      &.slide-down-enter,
      &.slide-down-leave-to {
        transform: translate3d(0, -100%, 0);
      }
      &.slide-down-enter-to,
      &.slide-down-leave {
        transform: translate3d(0, 0, 0);
      }
      &.slide-down-enter-active,
      &.slide-down-leave-active {
        transition: all 0.3s linear;
      }
      .van-hairline--bottom::after {
        border-bottom-width: 0px;
      }
      .van-icon {
        color: #888;
      }
      .van-nav-bar__left {
        width: 50px;
      }
      .van-nav-bar__right {
        .van-icon {
          color: black;
          margin-left: 17px;
        }
      }
    }
    .read-function {
      position: absolute;
      left: 0;
      bottom: 0;
      display: block;
      width: calc(100% - 30px);
      padding: 0 15px;
      overflow: hidden;
      background: white;
      //  过渡动画
      &.slide-up-enter,
      &.slide-up-leave-to {
        transform: translate3d(0, 100%, 0);
      }
      &.slide-up-enter-to,
      &.slide-up-leave {
        transform: translate3d(0, 0, 0);
      }
      &.slide-up-enter-active,
      &.slide-up-leave-active {
        transition: all 0.3s linear;
      }
      .function-top {
        width: 100%;
        overflow: hidden;
        float: left;
        padding: 10px 0;
        .fun-text {
          width: 100%;
          span:nth-child(1) {
            float: left;
          }
          span:nth-child(2) {
            float: right;
          }
        }
        .fun-schedule {
          z-index: 10;
          padding: 0 50px;
          margin-top: 8px;
          .van-slider {
            .van-slider__button {
              width: 12px;
              height: 12px;
            }
          }
        }
      }
      .function-bottom {
        width: 100%;
        overflow: hidden;
        height: 50px;
        ul {
          width: 100%;
          overflow: hidden;
          li {
            float: left;
            width: 33%;
            height: 50px;
            .lis-logo {
              overflow: hidden;
              width: 40px;
              .van-icon {
                margin-left: 10px;
              }
              p {
                display: block;
                overflow: hidden;
                text-align: center;
              }
              img {
                width: 20px;
                height: 20px;
                display: block;
                margin-left: 11px;
              }
            }
          }
          li:nth-child(2) {
            .lis-logo {
              margin: 0 auto;
            }
          }
          li:nth-child(3) {
            .lis-logo {
              float: right;
            }
          }
        }
      }
    }
  }
  .read-label {
    width: 100%;
    height: 100%;
    position: absolute;
    left: -555px;
    z-index: 33;
    transition: all 0.5s linear;
    top: 0;
    .books-label {
      width: 90%;
      height: 100%;
      z-index: 52;
      background: white;
      .label-top {
        overflow: hidden;
        height: calc(100% - 80px);
        border-bottom: 1px solid #f0f0f0;
        .label-fifle {
          width: 100%;
          float: left;
          overflow: hidden;
          background: #f8f7fc;
          .fifle-heard {
            width: calc(100% - 30px);
            padding: 0 15px;
            margin-top: 25px;
            overflow: hidden;
            .fifle-books1 {
              margin: 10px 0;
              overflow: hidden;
              .books1-fl {
                float: left;
                width: 56px;
                height: 71px;
              }
              .books1-fr {
                margin-left: 15px;
                float: left;
                height: 71px;
                overflow: hidden;
                p {
                  margin-top: 5px;
                  max-width: 200px;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                }
                p:nth-child(2) {
                  font-size: 12px;
                  color: #999;
                }
              }
            }
            .fifle-books2 {
              width: 100%;
              overflow: hidden;
              margin-bottom: 5px;
              span {
                float: left;
              }
              .book2-logo {
                float: right;
              }
            }
          }
        }
        .label-uls {
          height: calc(100% - 141px);
          overflow-y: auto;
          padding: 0 15px;
          position: relative;
          .fifle-uls {
            width: 100%;

            overflow: hidden;
            ul {
              overflow: hidden;
              li {
                float: left;
                width: 100%;
                border-bottom: 0.5px solid #f0f0f0;
                padding: 15px 0px;
                .fit-fl {
                  overflow: hidden;
                  float: left;
                  span:nth-child(2) {
                    margin-left: 15px;
                  }
                }
                .fit-fr {
                  float: right;
                }
              }
            }
          }
        }
      }
      .label-bottom {
        padding: 10px 30px;
        overflow: hidden;
        .label-btn {
          width: 100%;
          overflow: hidden;
          .cool {
            background: #1fb18c;
            color: white;
          }
          .btn1 {
            float: left;
            font-size: 16px;
            padding: 5px 25px;
            border-radius: 17px;
            margin-left: 20px;
          }
          .btn2 {
            float: right;
            margin-right: 20px;
            font-size: 16px;
            padding: 5px 25px;
            border-radius: 17px;
          }
        }
      }
    }
  }
}
</style>